		this.setState({
      loading: false,
      // status: '',
      query: '',
      // columns: [],
      // selectedColumns: [],
      // fixedColumns: [],
      // formattedColumns: {},
      // columnExtensions: [],
      // rows: [],
      // ticketStats: {},
      // ticketList: [],
		});
